            text1: '–ù–∞ –ø–µ—Ä–µ—Å–µ—á–µ–Ω–∏–∏ –ê–¥–º–∏—Ä–∞–ª—Ç–µ–π—Å–∫–æ–≥–æ –ø—Ä–æ—Å–ø–µ–∫—Ç–∞ –∏ –ì–æ—Ä–æ—Ö–æ–≤–æ–π —É–ª–∏—Ü—ã, –Ω–∞–ø—Ä–æ—Ç–∏–≤ –ê–ª–µ–∫—Å–∞–Ω–¥—Ä–æ–≤—Å–∫–æ–≥–æ —Å–∞–¥–∞, –æ—Ç–∫—Ä—ã–ª—Å—è —Ä–µ—Å—Ç–æ—Ä–∞–Ω Petrov-Vodkin ‚Äî –Ω–æ–≤—ã–π –º–∞—Å—à—Ç–∞–±–Ω—ã–π –ø—Ä–æ–µ–∫—Ç –ò–≥–æ—Ä—è –ú–µ–ª—å—Ü–µ—Ä–∞, —Å–æ–≤–ª–∞–¥–µ–ª—å—Ü–∞ —Ä–µ—Å—Ç–æ—Ä–∞–Ω–æ–≤ La Perla –∏ –≥–ª–∞–≤–Ω–æ–≥–æ –≤ –ü–µ—Ç–µ—Ä–±—É—Ä–≥–µ –ø—Ä–æ–ø–∞–≥–∞–Ω–¥–∏—Å—Ç–∞ –æ—Ç–µ—á–µ—Å—Ç–≤–µ–Ω–Ω—ã—Ö –º–æ—Ä–µ–ø—Ä–æ–¥—É–∫—Ç–æ–≤.\n –ü—Ä–æ–µ–∫—Ç –∞–Ω–æ–Ω—Å–∏—Ä–æ–≤–∞–ª–∏ –¥–∞–≤–Ω–æ ‚Äî –µ—â–µ –¥–≤–∞ –≥–æ–¥–∞ –Ω–∞–∑–∞–¥, ‚Äî –Ω–æ –∏–∑-–∑–∞ —Å–ª–æ–∂–Ω–æ—Å—Ç–µ–π —Å —Å–æ–≥–ª–∞—Å–æ–≤–∞–Ω–∏—è–º–∏ –æ—Ç–∫—Ä—ã—Ç–∏–µ –ø—Ä–∏—Ö–æ–¥–∏–ª–æ—Å—å –ø–æ—Å—Ç–æ—è–Ω–Ω–æ –ø–µ—Ä–µ–Ω–æ—Å–∏—Ç—å. –í –∏—Ç–æ–≥–µ –≤ –∂–∏–∑–Ω—å —É–¥–∞–ª–æ—Å—å –≤–æ–ø–ª–æ—Ç–∏—Ç—å –Ω–µ –≤—Å–µ –∏–∑–Ω–∞—á–∞–ª—å–Ω—ã–µ –∑–∞–¥—É–º–∫–∏ (–Ω–µ –≤—ã—à–ª–æ, –Ω–∞–ø—Ä–∏–º–µ—Ä, —Å —Ä—É—Å—Å–∫–æ–π –ø–µ—á—å—é, –∫–æ—Ç–æ—Ä—É—é –ø–ª–∞–Ω–∏—Ä–æ–≤–∞–ª–∏ —É—Å—Ç–∞–Ω–æ–≤–∏—Ç—å –≤ –æ–¥–Ω–æ–º –∏–∑ –∑–∞–ª–æ–≤), –Ω–æ —Ç–∞–∫ –∏–ª–∏ –∏–Ω–∞—á–µ —Ä–µ—Å—Ç–æ—Ä–∞–Ω –ø–æ–ª—É—á–∏–ª—Å—è –∫—Ä–∞—Å–∏–≤—ã–º –∏ –æ—Å–Ω–æ–≤–∞—Ç–µ–ª—å–Ω—ã–º. The Village –ø–æ–±—ã–≤–∞–ª –≤ –∑–∞–≤–µ–¥–µ–Ω–∏–∏ –∏ —Ä–∞—Å—Å–∫–∞–∑—ã–≤–∞–µ—Ç, –∫–∞–∫ –æ–Ω–æ —É—Å—Ç—Ä–æ–µ–Ω–æ.',
