            text1: '–°—É–ø —Ñ–æ-–±–æ —Å —Ç–æ–Ω–∫–æ –Ω–∞—Ä–µ–∑–∞–Ω–Ω–æ–π –≥–æ–≤—è–¥–∏–Ω–æ–π –∏ —Ä–∏—Å–æ–≤–æ–π –ª–∞–ø—à–æ–π –ø—Ä–∏–¥—É–º–∞–ª–∏ –Ω–∞ —Å–µ–≤–µ—Ä–µ –í—å–µ—Ç–Ω–∞–º–∞ –≤ –Ω–∞—á–∞–ª–µ –•–• –≤–µ–∫–∞. –°—á–∏—Ç–∞–µ—Ç—Å—è, —á—Ç–æ –µ–≥–æ –ø–æ—è–≤–ª–µ–Ω–∏–µ —Å—Ç–∞–ª–æ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–º –∫–æ–ª–æ–Ω–∏–∞–ª—å–Ω–æ–≥–æ –≤–ª–∏—è–Ω–∏—è –§—Ä–∞–Ω—Ü–∏–∏: –ø—Ä–µ–∂–¥–µ –∫–æ—Ä–æ–≤—ã –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–ª–∏—Å—å –≤–æ –í—å–µ—Ç–Ω–∞–º–µ –ª–∏—à—å –∫–∞–∫ —Ä–∞–±–æ—á–∏–µ –∂–∏–≤–æ—Ç–Ω—ã–µ, –∏ –∏—Ö –º—è—Å–æ –≤ –ø–∏—â—É –Ω–µ —É–ø–æ—Ç—Ä–µ–±–ª—è–ª–∏. –†–µ—Ü–µ–ø—Ç –º–µ–∂–¥—É —Ç–µ–º –±—ã—Å—Ç—Ä–æ –∑–∞–≤–æ–µ–≤–∞–ª –ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç—å –∏ —Ä–∞—Å–ø—Ä–æ—Å—Ç—Ä–∞–Ω–∏–ª—Å—è —Å–Ω–∞—á–∞–ª–∞ –ø–æ –≤—Å–µ–π —Å—Ç—Ä–∞–Ω–µ, –ø–æ–ª—É—á–∏–≤ –≤ –ø—Ä–æ—Ü–µ—Å—Å–µ —Ä–∞–∑–ª–∏—á–Ω—ã–µ —Ä–µ–≥–∏–æ–Ω–∞–ª—å–Ω—ã–µ —Ä–∞–∑–Ω–æ–≤–∏–¥–Ω–æ—Å—Ç–∏ (—Å–∞–º—ã–π –∏–∑–≤–µ—Å—Ç–Ω—ã–π –≤–∞—Ä–∏–∞–Ω—Ç ‚Äî —é–∂–Ω—ã–π —Ñ–æ-–±–æ, —Å–æ —Å–ª–∞–¥–∫–∏–º –ø—Ä—è–Ω—ã–º –±—É–ª—å–æ–Ω–æ–º –∏ –∑–µ–ª–µ–Ω—å—é), –∞ –ø–æ—Å–ª–µ ‚Äî –∏ –ø–æ –≤—Å–µ–º—É –º–∏—Ä—É.\n –í –ü–µ—Ç–µ—Ä–±—É—Ä–≥–µ –≤—Å–ª–µ–¥ –∑–∞ –ú–æ—Å–∫–≤–æ–π —Å–µ–π—á–∞—Å –Ω–∞–±–ª—é–¥–∞–µ—Ç—Å—è –Ω–∞—Å—Ç–æ—è—â–∏–π –±—É–º –≤—å–µ—Ç–Ω–∞–º—Å–∫–æ–π –∫—É—Ö–Ω–∏. –ó–∞ –ø–æ—Å–ª–µ–¥–Ω–∏–µ –ø–æ–ª–≥–æ–¥–∞ –≤ –≥–æ—Ä–æ–¥–µ –æ—Ç–∫—Ä—ã–ª–æ—Å—å —Å—Ä–∞–∑—É –Ω–µ—Å–∫–æ–ª—å–∫–æ –Ω–æ–≤—ã—Ö –∑–∞–≤–µ–¥–µ–Ω–∏–π, –∞ —Å–∞–º —Ñ–æ-–±–æ –∑–∞–Ω—è–ª –º–µ—Å—Ç–æ –æ–¥–Ω–æ–≥–æ –∏–∑ –≥–ª–∞–≤–Ω—ã—Ö –ø–æ—Ö–º–µ–ª—å–Ω—ã—Ö –±–ª—é–¥ –Ω–∞—Ä–∞–≤–Ω–µ —Å —Ç—Ä–∞–¥–∏—Ü–∏–æ–Ω–Ω—ã–º–∏ —Å–æ–ª—è–Ω–∫–æ–π, –±–æ—Ä—â–æ–º –∏, –Ω–∞–ø—Ä–∏–º–µ—Ä, –∫–∞–≤–∫–∞–∑—Å–∫–∏–º —Ö–∞—à–µ–º.'
